import { request } from 'utils';

const prefix = '/api/students';

export const getCodeStudent = id =>
  request({
    prefix,
    url: `/Student/GetByCode/${id}`,
    method: 'GET',
  });
export const getBonusType = () =>
  request({
    prefix: '/api/categories',
    url: `/BonusType`,
    method: 'GET',
  });

export const getBonusTypeChild = id =>
  request({
    prefix: '/api/categories',
    url: `/BonusType/GetBonusTypeChild`,
    params: {
      id,
    },
    method: 'GET',
  });

export const addItemAddReward = dataAddReward =>
  request({
    prefix,
    url: `/BonusNote/CreateBNNoteAndDetail`,
    data: {
      ...dataAddReward,
    },
    method: 'POST',
  });

export const getListDiscipline = params =>
  request({
    prefix,
    url: `/DisciplineNote/GetDisciplineStudents`,
    params: {
      ...params,
    },
    method: 'GET',
  });

export const getDisciplineBehaviorId = id =>
  request({
    prefix,
    url: `/DisciplineNote/${id}`,
    method: 'GET',
  });

export const getDisciplineBehavior = () =>
  request({
    prefix: '/api/categories',
    url: `/DisciplineBehavior`,
    method: 'GET',
  });

export const getDiscipline = () =>
  request({
    prefix: '/api/categories',
    url: `/Discipline`,
    method: 'GET',
  });

export const addDiscipline = data =>
  request({
    prefix,
    url: `/DisciplineNote/CreateDNNoteAndDetail`,
    data: {
      ...data,
    },
    method: 'POST',
  });
export const getDisciplineById = id =>
  request({
    prefix,
    url: `/DisciplineNote/${id}`,
    method: 'GET',
  });

export const deleteDisciplineById = id =>
  request({
    prefix,
    url: `/DisciplineNote/${id}`,
    method: 'DELETE',
  });



